@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Newsreader&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Newsreader, Arial;
  transition: all .3s ease;
}

body{
  scroll-behavior: smooth;
  overflow-x: hidden;
}

h1{
  color: red;
  -webkit-animation: fade 0.7s linear 1;
          animation: fade 0.7s linear 1;
  font-family: Roboto, Arial;
  text-align: center;
  letter-spacing: 1.5px;
  padding: 1rem;
  margin-top: 0.7rem;
  font-size: 18px;
}

h1::before{
  content: "";
  width: 15px;
  height: 15px;
  margin-top: 5px;
  margin-right: 0.3rem;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  background-color: red;
  -webkit-animation: blink 2s infinite;
          animation: blink 2s infinite;
}

h2{
  text-transform: uppercase;
  margin: 1rem;
  color: aliceblue;
  text-align: center;
  letter-spacing: 1px;
}

body{
  height: 100vh;
}

section#main{
  background-color: black;
  height: 100vh;
}

#main ul{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

i{
  margin-top: 1rem;
  font-size: 29px;
  color: white;
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
}

#main .scroll h3{
  color: white;
  font-size: 25px;
  margin-top: 5rem;
  -webkit-animation: fade 0.7s linear 1;
          animation: fade 0.7s linear 1;
}

#title{
  font-family: 'Josefin Sans', sans-serif;
  text-transform: capitalize;
  font-size: 2em;
  -webkit-animation: fade 0.7s linear 1;
          animation: fade 0.7s linear 1;
}

ul ol h3{
  text-align: center;
  text-transform: uppercase;
}

ul ol h4{
  text-align: center;
  padding-top: 0.5rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  width: 100%;
}

ul ol:nth-child(1){
  border: 2px solid red;
  padding: 1rem;
  background-color: red;
}

ul ol:nth-child(2){
  border: 2px solid blue;
  padding: 1rem;
  background-color: blue;
}

ul ol:nth-child(3){
  border: 2px solid green;
  padding: 1rem;
  background-color: green;
}

ul ol:nth-child(4){
  border: 2px solid grey;
  padding: 1rem;
  background-color: grey;
}

.card{
  list-style: none;
  height: 15%;
  background-color: transparent;
  padding: 1.5rem;
  border-radius: 5px;
  margin: 1rem;
  margin-top: 8rem;
  -webkit-animation: fade 0.7s linear 1;
          animation: fade 0.7s linear 1;
  color: white;
  /* box-shadow: -5px -5px 5px rgba(0, 0, 0, 0.3), 5px 5px 5px rgba(255, 255, 255, 0.3); */
}

li:hover{
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
  cursor: default;
}

li:nth-child(3) .card_name{
  color: #20cc68!important;
}

li:nth-child(1) .card_name{
  color: #ff3324!important;
}

li:nth-child(4) .card_name{
  color: silver!important;
}

li small{
  color: aqua!important;
}

.card_name{
  color: coral;
  font-size: 30px;
  font-family: 'Josefin Sans', sans-serif;
  letter-spacing: 1px;
  /* text-transform: uppercase; */
  text-align: center;
}

#main div{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

ul{
  width: 100%;
  /* background-color: red; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.card_total{
  letter-spacing: 1px;
  padding: 1rem 0;
  text-align: center;
  width: 100%;
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  font-size: 40px;
  text-shadow: 3px 3px 5px rgba(255, 255, 255, 0.3), -3px -3px 5px rgba(255, 255, 255, 0.3);
}

::-webkit-scrollbar{
  display: none;
}

@-webkit-keyframes blink{
  0%{
    opacity: 0;
  }

  25%{
    opacity: 1;
  }

  50%{
    opacity: 0;
  }

  75%{
    opacity: 1;
  }

  100%{
    opacity: 0;;
  }
}

@keyframes blink{
  0%{
    opacity: 0;
  }

  25%{
    opacity: 1;
  }

  50%{
    opacity: 0;
  }

  75%{
    opacity: 1;
  }

  100%{
    opacity: 0;;
  }
}

.green{
  background-color: green;
}

.red{
  background-color: red;
}

.grey{
  background-color: grey;
}

.blue{
  background-color: blue;
}

#totalsmall{
  font-size: 15px;
  font-weight: 600;
}

@media (max-width: 999px){
  #main ul li.card{
    width: 100%!important;
    margin: 0;
    box-shadow: none;
    background-color: transparent;
  }

  #main li:nth-child(5) .card_name, #main li:nth-child(5) .card_name span{
    color: cyan;
  }

  #main ul{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 5rem 0;
  }
  
  section#main{
    height: auto;
    padding-bottom: 2rem;
  }


  .card_name{
    font-size: 15px;
  }

  .card_total{
    font-size: 25px;
  }

  li:nth-child(6) .card_total{
    font-size: 16px;
  }

  li:nth-child(6) .card_name, li:nth-child(6) .card_name span{
    color: yellow;
  }

  li:nth-child(2) .card_name, li:nth-child(2) .card_name span{
    color: chartreuse;
  }

  li:nth-child(3) .card_name, li:nth-child(3) .card_name span{
    color: red;
  }

  li:nth-child(5) .card_name, li:nth-child(5) .card_name span{
    color: blue;
  }

  li:nth-child(4) .card_name, li:nth-child(4) .card_name span{
    color: lightgrey;
  }

  #title{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    width: 75%;
    font-size: 20px;
    text-align: center;
  }
  
  .animate{
    width: 50%;
    height: 5%;
    overflow: hidden;
    border-radius: 5px;
  }

  #titleanimate{
    width: 10%;
    border-radius: 5px;
    overflow: hidden;
    height: 5px;
    background-color: red;
    -webkit-animation: move 5s infinite linear;
            animation: move 5s infinite linear;
  }

  *{
    overflow-x: hidden;
  }

}

@-webkit-keyframes bounce{
  0%{
    -webkit-transform: translateY(0%);
            transform: translateY(0%)
  }
  
  50%{
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%)
  }
  
  100%{
    -webkit-transform: translateY(0%);
            transform: translateY(0%)
  }
}

@keyframes bounce{
  0%{
    -webkit-transform: translateY(0%);
            transform: translateY(0%)
  }
  
  50%{
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%)
  }
  
  100%{
    -webkit-transform: translateY(0%);
            transform: translateY(0%)
  }
}

@-webkit-keyframes move{
  0%{
    margin-left: -100%;
  }
  
  50%{
    margin-left: 100%;
  }

  100%{
    margin-left: -100%;
  }
}

@keyframes move{
  0%{
    margin-left: -100%;
  }
  
  50%{
    margin-left: 100%;
  }

  100%{
    margin-left: -100%;
  }
}

#totalsmall::before{
  content: "🔼";
}

.redcolor{
  color: red;
}

.greencolor{
  color: green;
}

.bluecolor{
  color: blue;
}

.greycolor{
  color: grey;
}

.yellowcolor{
  color: rgb(231, 184, 28);
}

@-webkit-keyframes fade{
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

@keyframes fade{
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

small{
  color: rgb(255, 0, 234);
  margin-left: 0.5rem;
}

small::before{
  content: '\f062';
  padding-right: 0.1rem;
  font-family: "FontAwesome";
}

small.no::before{
  content: ""!important;
}
